  left: 20%;
}

.shade {
  width: 65%;
  height: 35%;
  z-index: 2;
  background: #3f51b5;
  border-radius: 50%;
  position: absolute;
  top: 60%;
  left: 17%;
}

.shaper {
  width: 60%;
  height: 45%;
  z-index: 2;
  background: #5d2926;
  border-radius: 50%;
  position: absolute;
  top: 35%;
  left: 20%;
}

.mouth {
  text-shadow: 1px 5px 5px #e45f80;
  color: #e4bd12;
  text-align: center;
  width: 20%;
  height: 30%;
  z-index: 2;
  background: #950425;
  border-radius: 50%;
  font-family: kirby, sans-serif;
  font-size: 2em;
  animation: 5s linear infinite alternate bouche;
  position: absolute;
  top: 60%;
  left: 40%;
}

@keyframes bouche {
  from {
    transform: scale(.2);
  }

  to {
    transform: scale(1.9);
  }
}

body {
  min-height: 100vh;
  background: #e693a1;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

label, h1 {
  text-shadow: 1px 5px 5px #e45f80;
  color: #e4bd12;
  cursor: pointer;
  text-align: center;
  justify-content: center;
  margin: 2rem;
  font-family: kirby, sans-serif;
  font-size: 3em;
  font-weight: bold;
  transition: all .5s ease-in-out;
  display: flex;
}

.btn {
  text-shadow: 1px 5px 5px #e45f80;
  color: #e4bd12;
  width: 250px;
  height: 50px;
  cursor: pointer;
  background: #db6e89;
  border: none;
  border-radius: 5px;
  outline: none;
  justify-content: center;
  margin: 20px auto 10px;
  font-family: kirby, sans-serif;
  font-size: 1.5em;
  font-weight: bold;
  transition: all .2s ease-in;
  display: block;
}

btn:hover {
  background: #dd8198;
}

select {
  color: #f0f8ff;
  background-color: #dd8198;
