@font-face {
	font-family: 'kirby';
	src: url('./kirby-classic.ttf') format('truetype')
}


.box {
  position: relative; 
  margin: auto; 
  display: block; 
  margin-top: 3%; 
  width: 51.5vmax; 
  height: 31vmax; 
  background: #E693A1; 
  border: none; 
}



/* .head {
  background: #E693A1;
  width: 100%; 
  height: 100%; 
  border-radius: 50%;
  z-index: 1;  
}  */

.left-cheek {
  position: absolute;
  background: #D8747A; 
  top: 40%; 
  left: 15%; 
  width: 20%; 
  height: 20%; 
  border-radius: 50%;
}


.right-cheek {
  position: absolute;
  background: #D8747A; 
  top: 40%; 
  right: 15%; 
  width: 20%; 
  height: 20%; 
  border-radius: 50%; 
}

.left-eye {
  position: absolute; 
  border-radius: 50%; 
  width: 10%; 
  height: 30%; 
  top: 10%; 
  left: 35%; 
  background: #5D2926; 
}

.right-eye {
  position: absolute; 
  border-radius: 50%; 
  width: 10%; 
  height: 30%; 
  top: 10%; 
  right: 35%; 
  background: #5D2926;  
}


.pupil { 
  position: absolute; 
  border-radius: 50%; 
  width: 60%; 
  height: 45%; 
  top: 5%; 
  left: 20%; 
  background: white; 
  z-index: 3;
}/*All shapes are on top of the invisible box. So cheeks and eyes are on z-index: 1; pupil will be on z-index: 2, 'higher layer' */  

.shade {
  position: absolute; 
  border-radius: 50%; 
  background: #3F51B5; 
  top: 60%;
  left: 17%;
  width: 65%; 
  height: 35%;  
  z-index: 2;
 }

.shaper {
  position: absolute; 
  border-radius: 50%; 
  width: 60%; 
  height: 45%; 
  top: 35%; 
  left: 20%; 
  background:#5D2926; 
  z-index: 2;
}
.mouth {
  font-family: "kirby", sans-serif;
  text-shadow: 1px 5px 5px  #e45f80;
  color: #e4bd12;
  font-size: 2em;
  text-align: center;
  position: absolute; 
  background: #950425; 
  border-radius: 50%; 
  top: 60%; 
  left: 40%; 
  width: 20%; 
  height: 30%; 
  z-index: 2; 
  animation: 5s linear  infinite  alternate  bouche;
}

@keyframes bouche {
  from { 
    transform: scale(0.2);
  }
  to { 
    transform: scale(1.9);
  }
}


body {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  /* font-family: "Jost", sans-serif; */
  background: #E693A1;
}

label , h1{
  font-family: "kirby", sans-serif;
  text-shadow: 1px 5px 5px  #e45f80;
  color: #e4bd12;
  font-size: 3em;
  justify-content: center;
  display: flex;
  margin: 2rem;
  font-weight: bold;
  cursor: pointer;
  transition: 0.5s ease-in-out;
  text-align: center;
}
/* Stylisation du bouton */
.btn {
  font-family: "kirby", sans-serif;
  text-shadow: 1px 5px 5px  #e45f80;
  color: #e4bd12;
  width: 250px;
height: 50px; /* Définition de la hauteur du bouton */
margin: 10px auto; 
justify-content: center; 
display: block;
/* color: #fff; Couleur du texte */
background: #db6e89; /* Couleur de fond */
font-size: 1.5em; /* Taille de la police */
font-weight: bold; /* Épaisseur de la police */
margin-top: 20px; /* Définition de la marge en haut du bouton */
outline: none;
border: none; /* Suppression de la bordure */
border-radius: 5px; /* Arrondissement des coins */
transition: 0.2s ease-in; /* Animation de transition pour une interaction utilisateur plus agréable */
cursor: pointer;
}

/* Effet de survol pour le bouton */
btn:hover {
background: #dd8198; /* Changement de couleur de fond lors du survol  avec une couleur plus clair*/
}

select {
  font-size: 1.5em;
  color: aliceblue;
  display: flex;
  justify-content: center;
  margin: auto;
  background-color:#dd8198;
}


