.timer {
  width: 250px;
  height: 200px;
  background: #db6e89;
  /* color: #fff; */
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  font-family: "kirby", sans-serif;
  text-shadow: 1px 5px 5px  #e45f80;
  color: #e4bd12;
  font-size: 2rem;
}

.btnAudio {
  display: flex;
  width: 20%;
  height: 30%;
}

.hidden {
  display: none;
}

.close {

  display: flex;
  justify-content:end;
}
.btnClose {
  cursor: pointer;
  justify-content: center; 
  display: block;
  background: #db6e8900; /* Couleur de fond */
  border: none;
}

.bouche {
  display: flex;
  justify-content: center;
  padding: 2.8rem;
}




